<template>
	<div class="body">
		<div class="search_group_top flex_between">
			<div class="search_box">
				<el-input v-model="search_text" v-on:keyup.enter.native="getDataList" prefix-icon="el-icon-search" placeholder="搜索" clearable>
				</el-input>
			</div>
			<div class="search_box">
				<el-select v-model="value8" filterable prefix-icon="el-icon-search" placeholder="审批状态" clearable>
					<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
					</el-option>
				</el-select>
			</div>
		</div>
		<div class="main">
			<div class="table">
				<el-table :data="tableData" border style="width: 100%">
					<el-table-column sortable prop="id" label="序号" width="100">
					</el-table-column>
					<el-table-column sortable prop="name" label="审批编号" width="200">
					</el-table-column>
					<el-table-column sortable prop="name" label="审批标题" width="200">
					</el-table-column>
					<el-table-column sortable prop="response_partner" label="审批类型" width="180">
					</el-table-column>
					<el-table-column sortable prop="manager" label="审批类型人" width="180">
					</el-table-column>
					<el-table-column sortable prop="subscrib_fund" label="审批申请时间" width="150">
					</el-table-column>
					<el-table-column sortable prop="fund_scale" label="审批状态" width="180">
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="150">
						<template slot-scope="scope">
							<el-button @click="upload(scope.row)" type="text" size="small">查看</el-button>
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata flex_start">
							<img src="../../images/table_nodata.png">
							<span>没有对应数据</span>
						</div>
					</template>
				</el-table>
				<el-pagination @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="1000">
				</el-pagination>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'workOrderApprovalList',
		data() {
			return {
				search_text: '',
				value8: '',
				currentPage: 1,
				pageSize: 10,
				tableData: [{
					id: 1,
					name: '英雄联盟'       
				}],
				options: [
					{
						id: 1,
						value: '选项1',
						label: '全部'
					},
					{
						id: 2,
						value: '选项2',
						label: '待我审批'
					},
					{
						id: 3,
						value: '选项3',
						label: '我已审批'
					},
					{
						id: 4,
						value: '选项4',
						label: '我发起的审批'
					},
					{
						id: 5,
						value: '选项5',
						label: '抄送我的'
					},
					{
						id: 6,
						value: '选项6',
						label: '待修改'
					},
					{
						id: 7,
						value: '选项7',
						label: '我已修改'
					},
				],
			}
		},
		created() {

		},
		mounted() {},
		components: {},
		methods: {
			getDataList(){
				console.log(1);
			},
			// 分页功能模块
			changeCurrentPage(e) {
				this.currentPage = e;
			},
			everyPageSizeChange(e) {
				this.pageSize = e;
			},
		},
	}
</script>

<style scoped="scoped">
	.body {
		padding: 0 30px 30px 30px;
		font-size: 14px;
	}
	.main{
		margin-top: 30px;
	}
</style>


